@import 'core/variables';
@import 'core/reset';
@import 'components/code';
@import 'components/links';
@import 'layout/page';
@import 'layout/header';
@import 'layout/footer';
@import 'pages/index';

@font-face {
    font-family: roboto;
    src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
}

:root {
    --brand-font: roboto;
}

.header{
background-color: black;
display: flex;
flex-direction: row;
justify-content: space-between;
height: 10vh;
width: 100%;
position: sticky;
top: 0;
left: 0;
}

.nav {
    display: none;
}

.logocabecera {
    height: 7vh;
    padding: 10px;
    margin-top: 10px;
}


.hero{
    background-color: #16acf2;
    color: white;
    font-family: var(--brand-font);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    height: 50vh;
    text-align: left;  
    gap: 10px;
}

.botonplanes{
    background-color: #18d958;
    text-transform: uppercase;
    border-radius: 10px;
    padding: 5px;
    width: 80%;
    text-align: center;
    margin-left: 30px;
}
.secundaria {
    font-family: var(--brand-font);
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-left: 30px;
    margin-right: 30px; 
}

.secundaria h2 {
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 15px;
}

.h2{
    font-size: 50px;
    
}

.lista{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    list-style: none;
    gap: 3px;
    
}

.lista li {
    display: flex;
    text-align: left;
}

.p1{
    font-size: 25px;
}
.p2 {
    font-size: 12px;
 }

.p3 {
    font-size: 12px;
 }
 .imgbenefit {
    width: 60px;
 }

 .h2, .p1, .p2 {
    margin-left: 15px;
 }

.piedepagina {
    display: flex;
    background-color: black;
    color: white;
    font-family: var(--brand-font);
    height: 10vh;
    text-decoration: none;
    font-size: 12px;
    justify-content: space-between;
    gap: 5px;
    margin-top: 10px;
}

.footer-left{
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 5px;
    margin-left: 5px;
}

.copyright {
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: end;
    gap: 15px;
    margin-bottom: 15px;
    margin-right: 15px;
}

.bandera {
    width: 15px;
}

.footer-left a {
    text-decoration: none;
    color: white;
}

@media (min-width: 480px) {
    .lineas {
        display: none;
    }
    .nav {
        display: flex;
        color: white;
    }

    .nav-list {
        display: flex;
        list-style: none;
        justify-content: flex-end;
        gap: 10px;
        align-items: center;
        font-family: var(--brand-font); 
        font-weight: bold;
        margin: 25px;
    }
    
    .descargar::after {
        content: "|";
        margin-left: 15px;
    }

    .main {
        min-height: 100vh;
    }

    .hero{
        background-image: url(../imagenes-spotify/premium_dancer.png);
        width: 100%;
        height: 40vh;
        display: flex;
        gap: 30px;
        justify-content: flex-end;
        padding-bottom: 20px;
    }
    .h2 {
        font-size: 70px;
    }
    .botonplanes {
        width: 20%;
    }

    .lista {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .lista li {
       width: 40vw; 
    }
    
    .piedepagina {
        position: fixed;
        bottom: 0;
        display: flex;
        width: 100%;
}

}
